

























































import Vue from 'vue'
import {
  getAllMenus,
  deleteMenu
} from '@/services/menu'

export default Vue.extend({
  name: 'MenuIndex',
  data () {
    return {
      menus: [] // 菜单列表
    }
  },
  created () {
    this.loadAllMenus()
  },
  methods: {
    async loadAllMenus () {
      const { data } = await getAllMenus()
      if (data.code === '000000') {
        this.menus = data.data
      }
    },
    handleEdit (item: any) {
      // console.log('handleEdit')
      this.$router.push({
        name: 'menu-edit',
        params: {
          id: item.id
        }
      })
    },
    handleDelete (item: any) { // $index 遍历项索引，item 当前元素
      // console.log('handleDelete: ', item)
      this.$confirm('确认删除吗?', '删除提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        // 请求删除操作
        const { data } = await deleteMenu(item.id)
        if (data.code === '000000') {
          this.$message({
            type: 'success',
            message: '删除成功!'
          })
          this.loadAllMenus() // 更新数据列表
        }
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    }
  }
})
